    private createUserMail(allUsers: XRGetUser_AllUsers_GetUserListAck, user: string, reset: boolean, pl: string, askForName: boolean) {
        let that = this;
        let prl = parseInt(matrixSession.getCustomerSetting("passwordResetLimitation"));
        // if NaN or undefined then fallback to 3days
        let maxDuration = prl ? prl : 72;
        restConnection.postServer("user/" + user + "/token", {
            purpose: "password_reset",
            validity:maxDuration,
            reason:"password reset by admin"
        }).done(async function (token: any) {
            // Update list of existing users if necessary.
            await that.updateGlobalUserInfo();

            var userDetails = (allUsers.user.filter(o=>o.login == user));

            let additionnalParameters  =""
            if( askForName) {
                additionnalParameters = "&askForName=1";
                if(userDetails.length >0 ) {
                    additionnalParameters += "&first=" + userDetails[0].firstName+ "&last="+ userDetails[0].lastName ;
                }

            }
            let url = globalMatrix.matrixBaseUrl + "/resetPassword.jsp?user=" + user + "&passwordStrength=" + allUsers.passwordStrength + additionnalParameters + "&double=" + ((allUsers.needDoublePassword == 1 || matrixSession.oAuthOnly()) ? "1" : "0") + "&oAuthOnly=" + (matrixSession.oAuthOnly() ? "1" : "0") + "&token=" + token.token;


            var userinfo =  "You need to set your password(s) using this link: <a href='" + url +"'>"+url+"</a><br/><br/>" +
                "Once you set your password you can login here: <a href='" + globalMatrix.matrixBaseUrl + "'>" + globalMatrix.matrixBaseUrl + "</a><br/>" +
                "Your user id is: <b>" + user + "</b><br/><br/>"+
                "Note: you have " + maxDuration + " hours to do this. If you are too late ask your admin for a new link!";

                var message = ml.Mail.getCannedMessage(reset?"reset_pwd":"new_user", user, "");

                message = message.replace("_userinfo_", userinfo);
                message = message.replace("_projectlist_", pl);

                ml.Mail.sendMail(user, "", "", reset?"Password Reset":"Access to Matrix", message,undefined,undefined,1);
        });

    }

    private enableSavePwd(disable:boolean) {
        let pwp = $("#ud_pw3");
        let errorPwp = $("#errorPWP");
        let errorMatch = $("#errorMatch");
        let btn = $(".savePwButton");
        errorPwp.hide();
        errorMatch.hide();

        if ($("#ud_pw1").val() != $("#ud_pw1R").val())  {
            btn.button("disable");
            errorMatch.show();
        }  else if (pwp.length && (<string>pwp.val()).length === 0) {
            btn.button("disable");
        } else if (pwp.length && (<string>pwp.val()).indexOf(" ") !== -1) {
            btn.button("disable");
            errorPwp.show();
        } else {
            btn.button(disable?"disable":"enable");
        }

    }

    private setUserData( userId:string, jsonParam:string, userAddedCb:Function, noUserAddedCb:Function , signOut = true):JQueryDeferred<{}> {
        let res = $.Deferred();
        restConnection.putServer("user/" + userId, {
            json: jsonParam
        }).done(function (result) {
            if (userAddedCb) {
                userAddedCb();
            }
            if(signOut)
                {
                matrixSession.signOut(false);
                    ml.UI.showSuccess("The password has been updated!");
                }

            res.resolve();
        }).fail(function (errorMsg) {
            ml.UI.showError("Failed to create user!", errorMsg);
            if (noUserAddedCb) {
                noUserAddedCb();
            }
            res.reject();
        });
        return res;
    }

