    private generatePassword(btn?:JQuery) {
        var keylist1 = "ABCDEFGHIJKLMNPQRSTUVWXYZabcdefghijklmnpqrstuvwxyz123456789_@?+-";
        var keylist2 = "_@?+-";

        function gp(chars:string, count:number) {
            var pwd = '';
            for (var idx = 0; idx < count; idx++) {
                pwd += chars.charAt(Math.floor(Math.random() * chars.length));
            }
            return pwd;
        }

        var pw = gp(keylist1, 11) + gp(keylist2, 1) + gp(keylist1, 3);
        if (btn) {
            var target = btn.data("target");
            $("#" + target).val(pw);
            // make readable
            (<HTMLInputElement>$("#" + target).get(0)).type = 'text';
        }
        return pw;
    }

