import { IXPathTools, IXPathToolsComp } from "./MatrixLibInterfaces";

export { XPathTools };

class XPathTools implements IXPathTools {

    constructor() {
    }

    get( node:JQuery ) {
        var comp:IXPathToolsComp, comps:IXPathToolsComp[] = [];
        
        var xpath = '';
        var getPos = function(element:Node) {
            var position = 1, curNode:Node;
            if (element.nodeType == Node.ATTRIBUTE_NODE) { return null;}
            for (curNode = element.previousSibling; curNode; curNode = curNode.previousSibling){
                if (curNode.nodeName == element.nodeName) { ++position;}
            }
            return position;
        };
        var element:Node = node[0];
        if (element instanceof Document) {return '/';}
        for (; element && !(element instanceof Document); element = element.nodeType == Node.ATTRIBUTE_NODE ? (<Attr>element).ownerElement : element.parentNode) {
            comp = comps[comps.length] = <IXPathToolsComp>{};
            switch (element.nodeType) {
                case Node.TEXT_NODE:
                    comp.name = 'text()';
                    break;
                case Node.ATTRIBUTE_NODE:
                    comp.name = '@' + element.nodeName;
                    break;
                case Node.PROCESSING_INSTRUCTION_NODE:
                    comp.name = 'processing-instruction()';
                    break;
                case Node.COMMENT_NODE:
                    comp.name = 'comment()';
                    break;
                case Node.ELEMENT_NODE:
                    comp.name = element.nodeName;
                    break;
            }
            comp.position = getPos(element);
        }
        for (var i = comps.length - 1; i >= 0; i--) {
            comp = comps[i];
            xpath += '/' + comp.name.toLowerCase();
            if (comp.position !== null) {xpath += '[' + comp.position + ']';}
        }

        return xpath;
    };
    
    hardCopy(element:JQuery) {
        // note this is used only by selenium top copy the attributes of a doc
        $.each( element.find( "select"), function(idx, select) {
              var selected = $(select).val();
              $(select).find('option').prop('selected', false);
              $(select).find('option').removeAttr('selected');
              $(select).find("option[value='" + selected + "']").prop('selected', true);
              $(select).find("option[value='" + selected + "']").attr('selected', 'selected');

         });

        $.each( element.find( "input[type='checkbox']"), function(idx, cb) {
              $(cb).is(":checked")?$(cb).attr("checked","checked"):$(cb).removeAttr("checked");
        });
         return element.html();
    }                
};