import {IDHFSectionOptions} from "../../PluginManagerDocuments";
import {DropdownAbstractDocFieldHandler} from "./DropdownAbstractDocFieldHandler";


export type { IRemarkOptions, IDHFRemarksConfig };

interface IRemarkOptions extends IDHFSectionOptions {

}

interface IDHFRemarksConfig {
    default:object
}

export class RemarksDocFieldHandler extends DropdownAbstractDocFieldHandler {

    static default_config:IDHFRemarksConfig = {
        default:{}
    };
    getXmlValue(): string {
        let tt: { lines: string }[] = [];
        tt.push({lines: this.getData()});
        return JSON.stringify(tt);
    }

    getDefaultConfig(): any {
        return RemarksDocFieldHandler.default_config.default;
    }
}
