interface ISearchResult {
    itemId: string,
    version: number,
    title: string,
    fieldVal?: ISearchResultField[],
    downlinks: string[],
    uplinks: string[],
    labels: string[],
    creationDate?: string
    // There is a pseudo-field called creationDate which may or may not be here.
    // We often construct ISearchResult objects from needle searches, and the needle
    // result has creationDate (sometimes).
}
interface ISearchResultField { id: number, value: string }

interface IRestoreItemResult { item: string, version: number, response: XRPostProject_RestoreItem_UndeleteAnswer }
interface IUpdateCache { item: IItem, parent: string }


