    getItemAsync(itemId:string, ignoreErrors:boolean, includeHistory:boolean):JQueryDeferred<IItem> {
        let that = this;
        var res = $.Deferred();

        let history = includeHistory?"?history=true":"";
        if (this.dbCache.isFolder(itemId)) {
            this.restConnection.getProject("item/" + itemId + history, ignoreErrors).done(function (result:XRGetProject_ItemDetail_TrimItem) {
                var item = that.parseItemJSON(itemId, result);
                item['children'] = [];
                res.resolve(item);
            }).fail(function (error) {
                res.reject(error);
            });
        } else {
            this.restConnection.getProject("item/" + itemId + history, ignoreErrors).done(function(result: XRGetProject_ItemDetail_TrimItem) {
                //TODO update tree (e.g if title changed or parent?)
                var item = that.parseItemJSON(itemId, result);
                res.resolve(item);
            }).fail(function (error) {
                res.reject(error);
            });
        }
        return res;
    }

    getNeedlesAsync(searchExpr:string, up:boolean, down:boolean, fields:string, labels:boolean, ignoreFilters?:boolean):JQueryDeferred<IItem[]> {
        var res:JQueryDeferred<IItem[]> = $.Deferred();
        let params:IRestParam = {};
        if( up || down)
            params.links = up&&down?"down,up":up?"up":"down";

        if( fields)
            params.fieldsOut = fields
        if(labels)
            params.labels = 1 ;

        let items:IItem[] = [];
        params.search = "mrql:"+searchExpr;

        var labelFilter = ml.LabelTools.getFilter();
        if (!ignoreFilters && labelFilter && labelFilter.length > 0) {
            params.filter = labelFilter;
        }

        restConnection.postProject("needle" , params, true).done(function (results: XRGetProject_Needle_TrimNeedle) {
            $.each(results.needles, function(idx, needle) {
                var item:IItemGet = {
                    id: ml.Item.parseRef(needle.itemOrFolderRef).id,
                    title: needle.title,
                    type: ml.Item.parseRef(needle.itemOrFolderRef).type,
                    downLinks: [],
                    upLinks: [],
                    modDate: needle.lastModDate,
                    creationDate: needle.creationDate,
                    isUnselected: 0,
                    labels: needle.labels?needle.labels.split(",").map( function(label) { return label.substr(1, label.length-2);}) : []
                };

                if (needle.fieldVal) {
                    $.each( needle.fieldVal, function(jdx, fieldVal)  {
                       (<IGenericMap>item)[fieldVal.id.toString()] = fieldVal.value;
                    });
                }

                for (var idx = 0; needle.downLinkList && idx < needle.downLinkList.length; idx++) {
                    var tol = needle.downLinkList[idx].itemRef;

                    item.downLinks.push({to: ml.Item.parseRef(tol).id, title: needle.downLinkList[idx].title, modDate: needle.downLinkList[idx].modDate});
                }

                for (var idx = 0; needle.upLinkList && idx < needle.upLinkList.length; idx++) {
                    var tol = needle.upLinkList[idx].itemRef;

                    item.upLinks.push({to: ml.Item.parseRef(tol).id, title: needle.upLinkList[idx].title, modDate: needle.upLinkList[idx].modDate});
                }

                items.push(item);
            });
            res.resolve(items);
        }).fail( function() {
            res.reject();
        });

        return res;

    }

