    private parseSearchResult( needle:XRTrimNeedleItem ,fieldList?:string):ISearchResult {
        let fullitem = ml.Item.parseRef(needle.itemOrFolderRef);
        let sr:ISearchResult = {
            itemId: fullitem.id, version:fullitem.version, title: needle.title, downlinks:[],  uplinks:[],  labels:[]};
        if ( fieldList &&  fieldList.length>0) {
            sr.fieldVal = needle.fieldVal;
        }
        if (needle.downLinkList) {
            $.each( needle.downLinkList, function(idx,link) {
                sr.downlinks.push(ml.Item.parseRef(link.itemRef).id);
            });
        }
        if (needle.upLinkList) {
            $.each( needle.upLinkList, function(idx,link) {
                sr.uplinks.push(ml.Item.parseRef(link.itemRef).id);
            });
        }
        if (needle.labels) {
            let labels = needle.labels.split(",");
            $.each( labels, function(idx,label) {
                sr.labels.push(label.substr(1, label.length-2));
            });
        }
        return sr;
    }

    searchAsyncMinimalOutput(term: string, filter?: string, ignoreFilters?: boolean, crossProject?:string):JQueryDeferred<string[]>{
        var res = $.Deferred();
        // Add the minimal outputMode flag
        var params:XCGetNeedleMinimal= { search : term};

        if ( !ignoreFilters && filter && filter.length > 0) {
            params.filter = filter;
        }
        else{
                var labelFilter = ml.LabelTools.getFilter();
            if (!ignoreFilters && labelFilter && labelFilter.length > 0) {
                params.filter = labelFilter;
            }
        }

        if (params.search.length > 0) {
            if (crossProject) {

                this.restConnection.postServer(crossProject + "/needleminimal",  params, true).done(function (result: XRGetProject_NeedleMinimal_StringList) {
                    res.resolve(result);
                }).fail(function (jqxhr, textStatus, error) {
                    res.reject(jqxhr, textStatus, error);
                });
            } else {

                    this.restConnection.postProject("needleminimal", params, true ).done(function (result: XRGetProject_NeedleMinimal_StringList) {
                        res.resolve(result);
                    }).fail( function(jqxhr, textStatus, error) {
                    res.reject(jqxhr, textStatus, error);
                    });
                }

        } else {
            res.reject();
        }

        return <JQueryDeferred<string[]>> res;
    }
    searchAsync(term:string, filter?:string, ignoreFilters?:boolean, fieldList?:string, crossProject?:string, labels?:boolean, down?:boolean, up?:boolean, treeOrder?:boolean):JQueryDeferred<ISearchResult[]> {
        let that = this;

        var res = $.Deferred();
        var params:XCGetNeedle = {   search: term, id : "" };

        if (ignoreFilters && filter && filter.length > 0) {
            params.filter=  filter;
        }
        else{
            var labelFilter = ml.LabelTools.getFilter();
            if (!ignoreFilters && labelFilter && labelFilter.length > 0) {
                params.filter = labelFilter;
            }
        }


        if ( fieldList &&  fieldList.length>0) {
            params.fieldsOut = fieldList ;
        }
        if (treeOrder) {
            params.treeOrder= 1 ;
        }
        if (labels) {
            params.labels = 1 ;
        }
        if (down && up) {
            params.links = "up,down";
        } else if ( down ) {
            params.links  = "down" ;
        } else if (up) {
            params.links  = "up";
        }

        if (globalMatrix.ItemConfig.getTimeWarp()) {
            params.atDate = globalMatrix.ItemConfig.getTimeWarp();
        }

        if (params) {
            if (crossProject) {
                this.restConnection.postServer(crossProject + "/needle",  params, true).done(function(result: XRGetProject_Needle_TrimNeedle) {
                    var hoi:ISearchResult[] = [];
                    for (var idx = 0; idx < result.needles.length; idx++) {
                        hoi.push( that.parseSearchResult(result.needles[idx], fieldList));
                    }
                    res.resolve(hoi);
                }).fail( function(jqxhr, textStatus, error) {
                res.reject(jqxhr, textStatus, error);
                });
            } else {
                this.restConnection.postProject("needle?", params,true).done(function(result: XRGetProject_Needle_TrimNeedle) {
                    var hoi:ISearchResult[] = [];
                    for (var idx = 0; idx < result.needles.length; idx++) {
                        hoi.push( that.parseSearchResult(result.needles[idx], fieldList));
                    }
                    res.resolve(hoi);
                }).fail( function(jqxhr, textStatus, error) {
                res.reject(jqxhr, textStatus, error);
                });
            }
        } else {
            res.reject();
        }
        return <any>res;
    }


