    createItemAsync(itemJson: IItemPut, comment: string, actions: string, parentId: string, dontFailOnCleanup?:boolean): JQueryDeferred<IDBParent> {
        let that = this;
        var res2 = $.Deferred();
        if (itemJson.children) {
            let postItFolder:XCPostAddFolder = {
                label: itemJson.title,
                parent: parentId,
                reason: comment};
            for (var par in itemJson) {
                if (!itemJson.hasOwnProperty(par))
                    continue;
                if (postItFolder.hasOwnProperty(par))
                    continue;
                if (par === "type")
                    continue;
                if (par === "children")
                    continue;
                if (par === "title")
                    continue;
                if (par === "labels" || par === "linksUp"|| par === "linksDown") {
                    continue;
                }
                let c:string = par;
                (<IGenericMap>postItFolder)["fx" + par] =  (<IGenericMap>itemJson)[par];

            }
            if ( dontFailOnCleanup ) {
                postItFolder.failOnCleanup = 0;
            }
            this.restConnection.postProject("folder", postItFolder).done(function(result: XRPostProject_AddFolder_AddItemAck) {
                //TODO GET not serial BUT complete number like REQ-4 from rest
                itemJson.id = "F-" + itemJson.type + "-" + result.serial;
                res2.resolve(that.dbCache.insertItem(itemJson, parentId));
            });
        } else {
            let postItItem:XCPostAddItem = {
                title: itemJson.title,
                folder: parentId,
                reason: comment,
                linksUp:"",
                linksDown:""
                };
            for (var par in itemJson) {
                if (!itemJson.hasOwnProperty(par))
                    continue;
                if (postItItem.hasOwnProperty(par))
                    continue;
                if (par === "type")
                    continue;
                if (par === "labels") {
                    postItItem.labels = itemJson.labels;
                    continue;
                }
                if ( par === "linksUp" ) {
                    postItItem.linksUp = itemJson.linksUp;
                    continue;
                }
                if (  par === "linksDown") {
                    postItItem.linksDown = itemJson.linksDown;
                    continue;
                }
                (<IGenericMap>postItItem)["fx" + par] =  (<IGenericMap>itemJson)[par];
            }
            if ( dontFailOnCleanup ) {
                postItItem.failOnCleanup = 0;
            }

            this.restConnection.postProject("item", postItItem).done(function (result:XRPostProject_AddItem_AddItemAck) {
                itemJson.id = itemJson.type + "-" + result.serial;
                var idbparent = that.dbCache.insertItem(itemJson, parentId);
                res2.resolve(idbparent);
            });
        }
        return <any>res2;
    }

