    restoreItemAsync(itemId:string, title:string, version:number, comment:string):JQueryDeferred<IRestoreItemResult> {
        let that = this;
        var res = $.Deferred();
        var postIt: XCPostRestoreItem = {
            reason: comment
        };

        var arg = itemId;
        if (version) {
            postIt.at = version;
        }

        this.restConnection.postProject("item/" + arg, postIt).done(function(result: XRPostProject_RestoreItem_UndeleteAnswer) {

            that.dbCache.deleteItem(itemId);
            var itemJson:IItem = {title: title, id: itemId, type: ml.Item.parseRef(itemId).type};
            that.dbCache.insertItem(itemJson, result.newParent);
            res.resolve({itemId: itemId, version: version, response: result});
        });

        return <any>res;
    }


    updateItemAsync(itemJson: IItemPut, comment:string, auditAction:string, requireVersion?:number):JQueryDeferred<IItemGet> {
        let that = this;

        var res = $.Deferred();
        var putIt: XCPutEditItem = { reason: comment};
        if ( typeof itemJson.title !== "undefined") {
            putIt.title = itemJson.title;
        }
        if (auditAction) {
            putIt["auditAction"] = auditAction;
        }
        if (requireVersion) {
            putIt["currentVersion"] = requireVersion;
        }
        for (var par in itemJson) {
            if (!itemJson.hasOwnProperty(par))
                continue;
            if (putIt.hasOwnProperty(par))
                continue;
            if (par === "type")
                continue;
            if (par === "category")
                continue;
            if (par === "links")
                continue;
            if (par === "title")
                continue;
            if (par === "id")
                continue;


            if(isNaN(<any>par)){
                // it's attribute other than a field
                (<IGenericMap>putIt)[ par] = (<IGenericMap>itemJson)[par];
            }else{
                // it's a number so we assume it's a field
                (<IGenericMap>putIt)["fx" + par] =  (<IGenericMap>itemJson)[par];
            }
        }
        this.restConnection.putProject("item/" + itemJson.id, putIt, itemJson.id).done(function(result: XRPutProject_EditItem_TrimItem|string) {
            if (result !== "putEditItem | You need to set a title argument") {
                var item = <IItemGet>that.parseItemJSON(itemJson.id, <XRPutProject_EditItem_TrimItem>result);
                if (that.dbCache.isFolder(itemJson.id)) {
                    item['children'] = [];
                }

                that.dbCache.updateItem(<IItem>itemJson);

                res.resolve(item);
            } else {
                ml.Logger.log("error", "PUT of " + itemJson.id + " FAILED. Error was: putEditItem | You need to set a title argument");
            }
        }).fail(function () {
            // MATRIX-3815
            res.reject("saving failed");
        });

        return res;
    }

    moveItemsAsync(itemIds:string, newFolder:string, newPosition:number, comment:string):JQueryDeferred<{}> {
        let that = this;
        var res = $.Deferred();

        if (itemIds.indexOf(",") == -1 ) {
            var putIt = {
                reason: comment,
                newFolder: newFolder,
                newPosition: newPosition + 1
            };
            this.restConnection.putProject("item/" + itemIds, putIt).done(function () {
                that.dbCache.moveItem(itemIds, newFolder, newPosition);
                res.resolve();
            }).fail(function () {
                res.reject();
            });
        } else {
            let moveIt = {
                items:itemIds,
                reason:comment
            }
            this.restConnection.postProject("movein/" + newFolder, moveIt).done(function () {
                res.resolve();
            }).fail(function () {
                res.reject();
            });
        }

        return res;
    }

