import { ItemControl } from "../UI/Components/index";
import { IItem } from "./../../globals";
import { IDBParent } from "./DBCache";

export type {
    IItemViewEvent, IItemChangeEvent, IPreCreateItemEvent, IPreCreateCloseEvent, IGenericItemEvent,
    IGenericItemIdEvent, INewItemIdEvent, ILabelChangeEvent, ISignatureEvent, IEvent
};
export { EventDispatcher, EventDispatcherAsync, MR1 };

interface IItemViewEvent { caller: any, item: IItem, view: ItemControl }

interface IItemChangeEvent { caller: any, view: ItemControl, before: IItem, after: IItem }

interface IPreCreateItemEvent { caller: any, view: ItemControl, isItem: boolean, category: string }
interface IPreCreateCloseEvent { caller: any, ok: boolean }

interface IGenericItemEvent { caller: any, item: IItem }
interface IGenericItemIdEvent { caller: any, itemId: string }
interface INewItemIdEvent { caller: any, item: IDBParent }

interface ILabelChangeEvent { caller: any, item: IItem, set: string[], unset: string[] }
interface ISignatureEvent { caller: any, item: IItem, lastuser: boolean }

interface IEvent<TArgs, TMode> {
  subscribe(caller:any, fn: (args: TArgs) => TMode): void;
  unsubscribe(fn: (args: TArgs) => TMode): void;
  dispatch( args:TArgs):TMode;
}

class EventDispatcher<TArgs> implements IEvent<TArgs, void> {

    private _subscriptions: Array < ( args: TArgs) => void > = new Array < ( args: TArgs) => void > ();
    private _callers: Array < any > = new Array < any > ();

    subscribe(caller:any, fn: ( args: TArgs) => void): void {
        this._callers.push(caller);
        this._subscriptions.push(fn);
    }

    unsubscribe(fn: ( args: TArgs) => void): void {
        var i = this._subscriptions.indexOf(fn);
        if (i > -1) {
            this._subscriptions.splice(i, 1);
            this._callers.splice(i, 1);
        }
    }

    dispatch( args: TArgs): void {
        for (var i = 0; i < this._subscriptions.length; i++) {
            var handler = this._subscriptions[i];
            if (handler != null) {
                (<{caller:any}><any>args).caller = this._callers[i];
                handler(  args );
            }
        }
    }
}

class EventDispatcherAsync<TArgs> implements IEvent<TArgs, JQueryDeferred<{}>> {

    private _subscriptionsAsync: Array < ( args: TArgs) => JQueryDeferred<{}> > = new Array < ( args: TArgs) =>  JQueryDeferred<{}> > ();
    private _callersAsync: Array < any > = new Array < any > ();

    // now same for asyncs
    subscribe(caller:any, fn: ( args: TArgs) =>  JQueryDeferred<{}>): void {
        this._callersAsync.push(caller);
        this._subscriptionsAsync.push(fn);
    }
    unsubscribe( fn: ( args: TArgs) =>  JQueryDeferred<{}>): void {
        var i = this._subscriptionsAsync.indexOf(fn);
        if (i > -1) {
            this._subscriptionsAsync.splice(i, 1);
            this._callersAsync.splice(i, 1);
        }
    }

    dispatch( args: TArgs):JQueryDeferred<{}> {
        return this.dispatchAsyncOne(args,0);
    }
    private dispatchAsyncOne( args:TArgs, idx:number):JQueryDeferred<{}> {
        let that = this;

        let res = $.Deferred();
        if (idx >= this._subscriptionsAsync.length ) {
            res.resolve();
            return res;
        }

        var handler = this._subscriptionsAsync[idx];
        if (handler != null) {
            (<{caller:any}><any>args).caller = this._callersAsync[idx];
            handler( args ).done( function() {
                that.dispatchAsyncOne(args, idx+1).done( function() {
                    res.resolve();
                }).fail( function() {
                    res.reject();
                });
            }).fail( function() {
                res.reject();
            });
        } else {
            that.dispatchAsyncOne(args, idx+1).done( function() {
                res.resolve();
            }).fail( function() {
                res.reject();
            });
        }

        return res;
    }
}


export class MR1Impl {

    cbsItemDisplayed: EventDispatcher<IItemViewEvent> = new EventDispatcher<IItemViewEvent>();
    cbsCreateItemDisplayed: EventDispatcher<IPreCreateItemEvent> = new EventDispatcher<IPreCreateItemEvent>();
    cbsCreateItemDisplayedClose: EventDispatcher<IPreCreateCloseEvent> = new EventDispatcher<IPreCreateCloseEvent>();
    cbsBeforeSave: EventDispatcherAsync<IItemChangeEvent> = new EventDispatcherAsync<IItemChangeEvent>();
    cbsAfterSave: EventDispatcher<IItemChangeEvent> = new EventDispatcher<IItemChangeEvent>();
    cbsAfterRestore: EventDispatcher<IGenericItemIdEvent> = new EventDispatcher<IGenericItemIdEvent>();
    cbsAfterDelete: EventDispatcher<IGenericItemEvent> = new EventDispatcher<IGenericItemEvent>();
    cbsAfterCreate: EventDispatcher<INewItemIdEvent> = new EventDispatcher<INewItemIdEvent>();
    cbsAfterCreateSign: EventDispatcher<IGenericItemEvent> = new EventDispatcher<IGenericItemEvent>();
    cbsBeforeDelete: EventDispatcherAsync<IGenericItemEvent> = new EventDispatcherAsync<IGenericItemEvent>();
    cbsAfterLabelChange: EventDispatcher<ILabelChangeEvent> = new EventDispatcher<ILabelChangeEvent>();
    cbsAfterSignature: EventDispatcher<ISignatureEvent> = new EventDispatcher<ISignatureEvent>();

    // triggers

    triggerItemCreate(view: ItemControl, isItem: boolean, category: string) { this.cbsCreateItemDisplayed.dispatch(<IPreCreateItemEvent>{ view: view, isItem: isItem, category: category }); }
    triggerItemCreateClose(ok: boolean) { this.cbsCreateItemDisplayedClose.dispatch(<IPreCreateCloseEvent>{ ok: ok }); }
    triggerItemDisplayed(item: IItem, view: ItemControl) { this.cbsItemDisplayed.dispatch(<IItemViewEvent>{ item: item, view: view }); }
    triggerBeforeSaveAsync(view: ItemControl, before: IItem, after: IItem): JQueryDeferred<{}> { return this.cbsBeforeSave.dispatch(<IItemChangeEvent>{ view: view, before, after }); }
    triggerAfterSave(view: ItemControl, before: IItem, after: IItem) { this.cbsAfterSave.dispatch(<IItemChangeEvent>{ before: before, after: after, view: view }); }
    triggerAfterRestore(itemId: string) { this.cbsAfterRestore.dispatch(<IGenericItemIdEvent>{ itemId: itemId }); }
    triggerAfterDelete(item: IItem) { this.cbsAfterDelete.dispatch(<IGenericItemEvent>{ item: item }); }
    triggerAfterItemCreate(item: IDBParent) { this.cbsAfterCreate.dispatch(<INewItemIdEvent>{ item: item }); }
    triggerAfterCreateSign(item: IItem) { this.cbsAfterCreateSign.dispatch(<IGenericItemEvent>{ item: item }); }
    triggerBeforeDeleteAsync(item: IItem): JQueryDeferred<{}> { return this.cbsBeforeDelete.dispatch(<IGenericItemEvent>{ item: item }); }
    triggerAfterLabelChange(item: IItem, before: string[], after: string[]) { this.cbsAfterLabelChange.dispatch(<ILabelChangeEvent>{ item: item, set: before, unset: after }); }
    triggerAfterSignature(item: IItem, lastuser: boolean) { this.cbsAfterSignature.dispatch(<ISignatureEvent>{ item: item, lastuser: lastuser }); }

    // notifications

    onItemDisplayed(): IEvent<IItemViewEvent, void> { /* fired after item is rendered */ return this.cbsItemDisplayed; }
    onItemCreateDlgOpen(): IEvent<IPreCreateItemEvent, void> { /* fired after dialog is opened */ return this.cbsCreateItemDisplayed; }
    onItemCreateDlgClose(): IEvent<IPreCreateCloseEvent, void> { /* fired after dialog is closed */ return this.cbsCreateItemDisplayedClose; }
    onAfterSave(): IEvent<IItemChangeEvent, void> {  /* fired after item was saved */ return this.cbsAfterSave; }
    onAfterRestore(): IEvent<IGenericItemIdEvent, void> {  /* fired after item was restored */ return this.cbsAfterRestore; }
    onAfterDelete(): IEvent<IGenericItemEvent, void> {/* event after an item (or a folder) has been deleted */ return this.cbsAfterDelete; }
    onAfterCreate(): IEvent<INewItemIdEvent, void> {/* event after an item (or a folder) has been created */ return this.cbsAfterCreate; }
    onAfterCreateSign(): IEvent<IGenericItemEvent, void> {/* event after a SIGN item has been created */ return this.cbsAfterCreateSign; }
    onAfterLabelChange(): IEvent<ILabelChangeEvent, void> { return this.cbsAfterLabelChange; }
    onAfterSignature(): IEvent<ISignatureEvent, void> { return this.cbsAfterSignature; }

    // allowing to stop execution. need to return promise, reject() to stop resolve() to continue.
    onBeforeSaveAsync(): IEvent<IItemChangeEvent, JQueryDeferred<{}>> { /* event before  an item will be saved. */return this.cbsBeforeSave; }
    onBeforeDeleteAsync(): IEvent<IGenericItemEvent, JQueryDeferred<{}>> { /* event before  an item (or a folder) has been deleted. */return this.cbsBeforeDelete; }

}

var MR1: MR1Impl = new MR1Impl();
globalThis.MR1 = MR1;
