import {GenericDocAbstractFieldHandler} from "./GenericDocAbstractFieldHandler";
import {IDHFSectionOptions} from "../../PluginManagerDocuments";
export type { ITableOfContentOptions, IDHFTableOfContentConfig };

interface ITableOfContentOptions extends IDHFSectionOptions {
    format:string, render_toc?:boolean
}

interface IDHFTableOfContentConfig {
    default:ITableOfContentOptions
}
export class TableOfContentDocFieldHandler extends GenericDocAbstractFieldHandler {

    static  default_config:IDHFTableOfContentConfig = { default: { format:"\\o \"1-3\" \\h \\z \\u \\f"}}

    getDefaultConfig(): any {
        return TableOfContentDocFieldHandler.default_config.default;
    }

    getXmlValue(): string {
        let options:ITableOfContentOptions = { format : this.dhfFieldConfig.format };
        return JSON.stringify([options]);
    }
}
