    public calculateColorFrom(input:string):ICIColor
    {
        let colorIndex = 0;
        for(var i = 0 ; i< input.length ; i++)
        {
            colorIndex+= input.charCodeAt(i);
        }
        return this.colorScheme[colorIndex%this.colorScheme.length];
    }

