import {GenericDocFieldHandler} from "./GenericDocFieldHandler";
import {IDHFSectionOptions} from "../../PluginManagerDocuments";
import {GenericDocAbstractFieldHandler} from "./GenericDocAbstractFieldHandler";



export type { ISmartTextOptions, IDHFSmartTextConfig };

interface ISmartTextOptions extends IDHFSectionOptions {
    plaintext?:boolean, richtext?:boolean, abbreviations?:boolean, terms?:boolean, customerTags?:boolean, projectTags?:boolean, includeAll?:boolean,
    firstColumnName:string, secondColumnName:string
}

interface IDHFSmartTextConfig {
    default:ISmartTextOptions
}



export class SmartTextDocFieldHandler extends GenericDocAbstractFieldHandler {
    static default_config:IDHFSmartTextConfig = {default:{
            plaintext:false, richtext:false, abbreviations:true, terms:true, customerTags:true, projectTags:true, includeAll:false,
            firstColumnName:"", secondColumnName:""
        }};
    getDefaultConfig() {
        return SmartTextDocFieldHandler.default_config.default;
    }
    getXmlValue(): string {
        let options:ISmartTextOptions = {
            abbreviations: this.dhfFieldConfig.abbreviations,
            terms:this.dhfFieldConfig.terms,
            richtext:this.dhfFieldConfig.richtext,
            plaintext: this.dhfFieldConfig.plaintext,
            includeAll: this.dhfFieldConfig.includeAll,
            customerTags: true,
            projectTags: true,
            firstColumnName:this.dhfFieldConfig.firstColumnName?this.dhfFieldConfig.firstColumnName:"",
            secondColumnName:this.dhfFieldConfig.secondColumnName?this.dhfFieldConfig.secondColumnName:""
        }
        return JSON.stringify([options]);

    }

}
