// -----------------------------------------------------------
// Tools to show reference dialog (with up and downtraces)
// -----------------------------------------------------------

/// <reference types="matrixrequirements-type-declarations" />
import { app, globalMatrix, IItem, IReference, matrixSession } from "../../../globals";
import { ml } from "../../matrixlib";
import { SelectMode } from "../Components/ProjectViewDefines";
import { refLinkStyle, refLinkTooltip } from "../Parts/RefLinkDefines";
import { ItemCreationTools } from "./ItemCreationView";
import { ItemSelectionTools } from "./ItemSelectionView";

import { UIToolsConstants } from "../../matrixlib/MatrixLibInterfaces";

export type { IReferenceToolsOptions };
export { ReferenceTools };

interface IReferenceToolsOptions { item: IItem, canEdit: boolean, callback?: (item: IItem) => void }

class ReferenceTools {


    showReferenceDialog (options:IReferenceToolsOptions) {
        if( options.callback == undefined)
        {
            //Make sure we have a callback
            options.callback = (item:IItem)=>{};
        }
        var treeContainer = $('<div class="Container nodeContainer"></div>');

        let riskCategories = globalMatrix.ItemConfig.getFieldsOfType("risk2").map(function (rc) { return rc.category });

        var allDownTypes: DrawTreeType[] = [];
        $.each(globalMatrix.ItemConfig.getLinkTypes(options.item.type, true, true), function (idx, type) {
            allDownTypes.push({type: type});
        });
        $.each(globalMatrix.ItemConfig.getLinkTypes(options.item.type, true, false), function (idx, type) {
            allDownTypes.push({type: type});
        });

        var allUpTypes: DrawTreeType[] = [];
        $.each(globalMatrix.ItemConfig.getLinkTypes(options.item.type, false, true), function (idx, type) {
            if (riskCategories.indexOf( type ) == -1) {
                allUpTypes.push({type: type});
            }
        });
        $.each(globalMatrix.ItemConfig.getLinkTypes(options.item.type, false, false), function (idx, type) {
            if (riskCategories.indexOf( type ) == -1) {
                allUpTypes.push({type: type});
            }
        });

        var rootDown: DrawTreeNode;
        var rootUp: DrawTreeNode;
        function hideXTCs(nodes: DrawTreeNode[]) {
            let hideFrom = 1000;
            let hideTo = -1000;
            for (var idx = 0; idx<nodes.length; idx++) {
                // assume that all XTCs come one after the other...
                if (ml.Item.parseRef(nodes[idx].id).type == "XTC") {
                    if (idx<hideFrom) hideFrom = idx;
                    if (idx>hideTo) hideTo = idx;
                }
            }
            if (hideTo-hideFrom>2) {
                // at least 4 XTCs
                let dotdotdot: DrawTreeNode = {
                    isRoot:false,
                    isOutDate:false,
                    Class:"nodesExpand",
                    Nodes:[],
                    ToolTip:"click to expand",
                    Content:$("<span>...</span>")
                }
                dotdotdot.Hidden = nodes.splice(hideFrom+1, hideTo-hideFrom-1);
                nodes.splice(hideFrom+1,0,dotdotdot);
            }
        }
        function createStartNode(item:IItem):void {
            var itemDate = new Date(item.modDate);
            var missingDownLinks = app.getMissingDownLinks(item);
            let evalDownlinks = app.evaluateTraceRule(item, true);

            rootDown = {isRoot: true, isDown: true, id: item.id, Content: $("<div></div>").refLink({
                    id: item.id, title: "", style: refLinkStyle.show, tooltip: refLinkTooltip.html, hideTitle:true
                }), Nodes: []};

            for (var idx = 0; idx < item.downLinks.length; idx++) {
                var refDate = new Date(item.downLinks[idx].modDate);

                rootDown.Nodes.push({isCreate: false, isOutDate: (refDate < itemDate), isDown: true,  isIndirect: item.downLinks[idx].isIndirect, id: item.downLinks[idx].to, Content: $("<div></div>").refLink({
                        id: item.downLinks[idx].to, title: "", style: refLinkStyle.show, tooltip: refLinkTooltip.html, hideTitle:true
                    }), Nodes: []});
            }
            hideXTCs(rootDown.Nodes);
            if (matrixSession.isEditor() && options.canEdit) {
                for (var idx = 0;idx < missingDownLinks.length; idx++) {
                    let required = (evalDownlinks.missingMustHaveCategories.indexOf(missingDownLinks[idx])!=-1)?"nodeCreate":"nodeCreateOptional";

                    rootDown.Nodes.push({isCreate: true, type: missingDownLinks[idx], isDown: true, Class: required, ToolTip: "Create new linked item", Content: $("<div>").html(missingDownLinks[idx]), Nodes: []});
                }
            }
            var missingUpLinks = app.getMissingUpLinks(item);
            let upRequired = app.evaluateTraceRule(item, false);
            rootUp = {isRoot: true, isDown: false, id: item.id, Content: $("<div></div>").refLink({
                    id: item.id, title: "", style: refLinkStyle.show, tooltip: refLinkTooltip.html, hideTitle:true
                }), Nodes: []};
            for (var idx = 0; idx < item.upLinks.length; idx++) {
                var refDate = new Date(item.upLinks[idx].modDate);
                rootUp.Nodes.push({isCreate: false, isOutDate: (refDate > itemDate), isDown: false, isIndirect: item.upLinks[idx].isIndirect, id: item.upLinks[idx].to, Content: $("<div></div>").refLink({
                        id: item.upLinks[idx].to, title: "", style: refLinkStyle.show, tooltip: refLinkTooltip.html, hideTitle:true
                    }), Nodes: []});
            }
            if (matrixSession.isEditor() && options.canEdit) {
                for (var idx = 0; idx < missingUpLinks.length; idx++) {
                    let required = (upRequired.missingMustHaveCategories.indexOf(missingUpLinks[idx])!=-1)?"nodeCreate":"nodeCreateOptional";

                    rootUp.Nodes.push({isCreate: true, type: missingUpLinks[idx], isDown: false, Class: required, ToolTip: "Create new linked item", Content: $("<div>").html(missingUpLinks[idx]), Nodes: []});
                }
            }
            if (allUpTypes.length > 0 && matrixSession.isEditor() && options.canEdit) {
                rootUp.Nodes.push({isSelect: true, types: allUpTypes, isDown: false,  Class: "nodeCreate", ToolTip: "Select linked item", Content: $("<div>").html("Select"), Nodes: []});
            }
            if (allDownTypes.length > 0 && matrixSession.isEditor() && options.canEdit) {
                rootDown.Nodes.push({isSelect: true, types: allDownTypes, isDown: true, Class: "nodeSelect", ToolTip: "Select linked item", Content: $("<div>").html("Select"), Nodes: []});
            }
        }

        function RefreshTree() {
            var oldContainer = treeContainer;
            oldContainer.hide();
            treeContainer = $('<div class="Container nodeContainer"></div>');
            app.dlgForm.append(treeContainer);
            DrawTree({
                Container: treeContainer,
                RootDown: rootDown,
                RootUp: rootUp,
                Layout: "Horizontal",
                OnNodeClick: NodeClick,
                OnNodeDoubleClick: NodeDoubleClick});
            oldContainer.html("");
            var ph = treeContainer.parent().height();
            var pw = treeContainer.parent().width();
            var nh = treeContainer.height();
            var nw = treeContainer.data("maxWidth");
            if (ph > nh) {
                treeContainer.css({top: (ph - nh) / 2});
            }
            if (pw > nw) {
                treeContainer.css({left: (pw - nw) / 2});
            }
            treeContainer.width(nw);
        }

        function NodeClick(theNode: DrawTreeNode, event: MouseEvent) {
            if (theNode.Class == "nodesExpand") {
                theNode.Nodes = ml.JSON.clone(theNode.Hidden);
                theNode.Class = "nodesExpanded";
                RefreshTree();
            } else if (theNode.Class == "nodesExpanded") {
                // toggle XTCs
                theNode.Nodes = null;
                theNode.Class = "nodesExpand";
                RefreshTree();
            } else if (theNode.isSelect) {
                app.dlgForm.dialog("close");
                var select = new ItemSelectionTools();
                select.showDialog({
                    linkTypes: theNode.types,
                    getSelectedItems: async function () {
                        return theNode.isDown ? options.item.downLinks : options.item.upLinks;
                    },
                    selectionChange: function(newRefs: IReference[]) {
                        var oldRefs = theNode.isDown ? options.item.downLinks : options.item.upLinks;
                        // figure out if the old refs have some links to some items which cannot be selected in tree (e.g. uplinks as risk controls -> these should not be removed)
                        let selectableTypes = (theNode.isDown?allDownTypes:allUpTypes).map( function(st) {return st.type;});
                        $.each(oldRefs, function(oridx,oref) {
                            if (selectableTypes.indexOf( ml.Item.parseRef(oref.to).type) ==-1) {
                                newRefs.push( oref );
                            }
                        });
                        var changes = ml.Item.updateReferences(oldRefs, newRefs, theNode.isDown ? options.item.id : null, theNode.isDown ? null : options.item.id);
                        app.commitChangeListAsync(changes).always(function (error, stepsDone) {
                            options.callback(options.item);
                        });


                    },
                    selectMode:SelectMode.items,
                });

            } else if (theNode.isCreate) {

                if (app.canCreateItemType(theNode.type, false)) {
                    app.dlgForm.dialog("close");
                    var create = new ItemCreationTools();
                    create.showDialog({
                        name: globalMatrix.ItemConfig.getItemConfiguration(theNode.type).label,
                        parent: app.getRootOfType(theNode.type),
                        type: theNode.type,
                        folder: false,
                        dontOpenNewItem:true,
                        created: function(created) {
                            var fromId:string;
                            var toId:string;
                            if (theNode.isDown) {
                                fromId = options.item.id;
                                toId = created.to;
                            } else {
                                fromId = created.to;
                                toId = options.item.id;
                            }
                            app.addDownLinkAsync(fromId, toId).done(function () {

                            }).fail(function () {
                                ml.UI.showError("Could not link items.", "");
                            }).always(function(){
                                 options.callback(options.item);
                            });
                        }


                    });
                }
            } else if ( event.ctrlKey) {
                var win = window.open(app.createItemUrl(theNode.id), '_blank');
                if (event.preventDefault)
                    event.preventDefault();
                if (event.stopPropagation)
                    event.stopPropagation();
                return;
            } else if (theNode.Nodes.length == 0) {

                app.getItemAsync(theNode.id).done(function (item) {
                    if (theNode.isDown) {
                        for (var idx = 0; idx < item.downLinks.length; idx++) {
                            theNode.Nodes[idx] = {
                                isCreate: false, isDown: true, isIndirect: item.downLinks[idx].isIndirect, id: item.downLinks[idx].to, Content: $("<div></div>").refLink({
                                    id: item.downLinks[idx].to, title: "", style: refLinkStyle.show, tooltip: refLinkTooltip.html, hideTitle:true
                                }), Nodes: []};
                        }
                        hideXTCs(theNode.Nodes);
                    } else {
                        for (var idx = 0; idx < item.upLinks.length; idx++) {
                            theNode.Nodes[idx] = {isCreate: false, isDown: false, isIndirect: item.upLinks[idx].isIndirect,id: item.upLinks[idx].to, Content: $("<div></div>").refLink({
                                    id: item.upLinks[idx].to, title: "", style: refLinkStyle.show, tooltip: refLinkTooltip.html, hideTitle:true
                                }), Nodes: []};
                        }
                    }
                    // Draw the tree for the first time
                    RefreshTree();
                });
            }
        }

        function NodeDoubleClick(theNode: DrawTreeNode, event: MouseEvent) {
            if(theNode.id == undefined){
                console.warn("No id defined for node. Cannot open item. It's problably double click on select or create.");
                return;
            }

            if ( event.ctrlKey ||event.metaKey ) {
                var win = window.open(app.createItemUrl(theNode.id), '_blank');
                if (event.preventDefault)
                    event.preventDefault();
                if (event.stopPropagation)
                    event.stopPropagation();
                return;
            }
            app.dlgForm.dialog("close");
            app.treeSelectionChangeAsync(theNode.id).done(function () {
            }).fail(function () {
            }).always(function(){ options.callback(options.item);});
        }



        ml.UI.showDialog(
            app.dlgForm,
            "References of '" + options.item.id + "'",
            treeContainer, 900, 400,
            [{
                text:'Close',
                class:'btnCancelIt',
                click:function () {
                    app.dlgForm.dialog("close");
                }
            }], UIToolsConstants.Scroll.Auto, false, true, null, null,
            function () {
                app.dlgForm.resizeDlgContent([]);
                RefreshTree();
            }
        );

        /*
        app.dlgForm.dialog({
            autoOpen: true,
            title: "References of '" + options.item.id + "'",
            height: 400,
            width: 900,
            modal: true,
            resizeStop: function () {
                app.dlgForm.resizeDlgContent([]);
                RefreshTree();
            },
            open: function () {},
            buttons: []
        });
       */
        app.getItemAsync(options.item.id).done(function (item: IItem) {
            createStartNode(item);
            RefreshTree();
        })

    };
}
