import { FieldDescriptions } from "../FieldDescriptions";
import { XRFieldTypeAnnotatedParamJson } from "../ItemConfiguration";
import {IFieldHandler} from "./IFieldHandler";
export { TextlineFieldHandler };

class TextlineFieldHandler implements IFieldHandler {
    private data = "";
    private config: XRFieldTypeAnnotatedParamJson;

    constructor(configIn: XRFieldTypeAnnotatedParamJson) {
        this.config = configIn;
    }

    getFieldType(): string { return FieldDescriptions.Field_textline; }

    initData(serializedFieldData: string) { this.data = serializedFieldData; }
    getData(): string { return this.data; }

    getText(): string { return this.data; }
    setText(str: string) {
        // make sure there are no newlines.
        this.data = str.replace("\n", "");
    }
}
